import { gql } from 'apollo-boost';

export const USERS = gql`
    query users($limit: Int = 15){
        users(limit: $limit) {
            id,
            username,
            createdAt,
            updatedAt,
            count
        }
    }
`;

export const SITELIST = gql`
  query sites($limit: Int = 10) {
    sites(limit: $limit) {
        id,
        name,
        location,
        createdAt,
        updatedAt,
        count
        manager {
            name: username
        }
    }
  }
`;

export const USERS_SITELIST = gql`
    query users_sites($limit: Int = 15){
        users(limit: $limit) {
            id,
            username,
            createdAt,
            updatedAt,
            count
        }
        sites(limit: $limit) {
            id,
            name,
            location,
            createdAt,
            updatedAt,
            count,
            manager {
                username
            }
        }
    }
`;

export const SITE = gql`
    query site($id: String!, $limit: Int!, $skip: Int){
        site(id: $id, limit: $limit, skip: $skip){
            id,
            name,
            location,
            cost,
            count,
            entries
            {
                id,
                createdAt,
                updatedAt,                
                mistri{
                    quantity,
                    cost
                },
                labour{
                    quantity,
                    cost
                },
                eit{
                    quantity,
                    cost
                },
                morang{
                    quantity,
                    cost
                },
                githi{
                    quantity,
                    cost
                },
                cement{
                    quantity,
                    cost
                },
                saria{
                    quantity,
                    cost
                },
                dust{
                    quantity,
                    cost
                },
                other{
                    quantity,
                    cost
                },
                total
            }
        }
    }
`



